.wrapper {
  // min-height: 100%;
  display: flex;
  flex-direction: column;
  // overflow: hidden;
}

.header {
  position: fixed;
  z-index: 10;
  width: 100%;  

  &-top {
    background-color: $primary;    
    &-logo {
      width: 220px;
      height: 45px;
      margin-top: auto;
      margin-bottom: auto;
    }
    &-wrapper {
      display: flex;
      align-items: center;
      height: 80px;
    }
    &-contact {
      display: flex;
      align-items: center;
      // gap: 40px;
      margin-left: auto;
      margin-right: 40px;
      @include fz(18);
      letter-spacing: 0.72px;

      .address {
        display: flex;
        align-items: center;
        gap: 12px;
        color: #fff;
        margin-right: 46px;
      }

      .phone {
        display: flex;
        align-items: center;
        gap: 20px;
        margin-right: 20px;
        a {
          display: inherit;
          color: #fff;
          &.viber, &.tel {
            width: 32px;
            height: 32px;
          }
        }
      }

      .socials {
        display: flex;
        align-items: center;
        a {
          width: 32px;
          height: 32px;
          display: inherit;          
        }
      }
    }
  }
  &-bottom {
    background-color: $bgc-pink;
    
    .header-nav {
      display: flex;
      align-items: center;
      gap: 60px;
      color: #fff;
      height: 60px;
      &-item {  
        height: 100%;
        &:hover {
          ul {
            height: max-content;
            opacity: 1;
            transform: translateY(0);
            overflow: initial;
            transition: all 0.8s;
          }
          .trigger {
            &-arrow {
              transform: rotate(180deg);
            }
          }    
        }
        .trigger {
          height: 100%;
          display: flex;
          align-items: center;
          gap: 6px;
          cursor: pointer;
          &-text {
            height: 100%;
            @include fz(18);
            font-weight: 500;
            text-transform: uppercase;
            line-height: 60px;
            letter-spacing: 0.72px;
            position: relative;

            &::after {
              content: '';
              position: absolute;
              left: 0;
              bottom: 0;
              height: 8px;
              width: 0%;
              background-color: $bgc;
              border-radius: 4px 4px 0px 0px;
              transition: width 0.3s ease-in-out;
            }

            &:hover {
              &::after {
                width: 100%;
              }
            }

            &.active {  
              position: relative;   
              &::after {               
                width: 100%;                
              }        
            }
          }
          &-arrow {
            transition: all 0.4s;
          }
        }     
        ul {
          position: absolute;     
          top: 50px;  
          height: 0;
          opacity: 0; 
          transform: translateY(-10px);
          overflow: hidden; 
          background-color: #fff; 
          box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);   
          border-radius: 12px;    
          padding: 20px 20px;  
          padding-right: 0;       
          transition: all 0.3s;
          z-index: 3;
          li {    
            margin-right: 20px;          
            a {
              width: 100%;  
              color: $font;
              padding: 12px 20px;                   
              display: inline-block;
              @include fz(18);
              font-weight: 500;
              letter-spacing: 0.72px;
              text-transform: uppercase;
              transition: all 0.5s;
              &:hover {
                color: #fff;                
                background-color: $primary;                      
                border-radius: 8px; 
              }
            }            
          }
        }
        // &.show {
        //   .trigger {
        //     &-arrow {
        //       transform: rotate(180deg);
        //     }
        //   }     
        //   ul {           
        //     height: max-content;
        //     opacity: 1;
        //     transform: translateY(0);
        //     overflow: initial;
        //     transition: all 0.3s;
        //   }
        // }
        .sub-menu {   
          margin-right: -20px;
          &-wrapper {
            position: relative; 
          }  
          a {
            display: flex;
            justify-content: space-between;
            align-items: center; 
            transition: all 0.4s;            
            &:hover {
              border-top-right-radius: 0;
              border-bottom-right-radius: 0;
              svg {
                visibility: hidden;
              }
            }           
          }                   
          ul {
            display: none;
            position: absolute;
            max-height: 500px;
            overflow: auto;
            right: -20px;  
            top: -20px;
            transform: translateX(100%);                    
            background-color: #fff;   
            padding-left: 0;
            li {
              a {
                background-color: #fff;
                color: $font;
                padding: 12px 20px; 
                padding-left: 40px;  
                text-transform: unset; 
                max-width: 330px;            
              }  
              &:hover {
                a {
                  color: #fff;                
                  background-color: $primary;  
                  width: 100%;
                  border-top-left-radius: 0;
                  border-bottom-left-radius: 0;  
                  border-top-right-radius: 8px; 
                  border-bottom-right-radius: 8px;
                }
              }            
            }         
          }
          &:hover {            
            ul {
              display: block;
              width: max-content;
            }            
          }
        }
      }  
      &-pages {
        ul {
          display: flex;
          gap: 60px;
          li {
            a {
              display: inline-block;
              height: 60px;
              line-height: 60px;
              @include fz(18);
              color: #fff;
              text-transform: uppercase;
              position: relative;
              letter-spacing: 0.72px;

              &::after {
                content: '';
                  position: absolute;
                  left: 0;
                  bottom: 0;
                  height: 8px;
                  width: 0%;
                  background-color: $bgc;
                  border-radius: 4px 4px 0px 0px;
                  transition: width 0.3s ease-in-out
              }
              &:hover {          
                &::after {                  
                  width: 100%;                  
                }
              }
              &.active {
                position: relative;
                
                &::after {
                  content: '';
                  position: absolute;
                  left: 0;
                  bottom: 0;
                  height: 8px;
                  width: 100%;
                  background-color: $bgc;
                  border-radius: 4px 4px 0px 0px
                }      
              }
            }
          }
        }       
      }  
      &-search {
        margin-left: auto;
        cursor: pointer;
        position: relative;
      }  
      .search-wrapper {       
        overflow: hidden;
        visibility: hidden;
        transform: translateY(-10px);
        display: flex;
        align-items: center;
        gap: 10px;
        position: absolute;
        right: 0;
        padding: 12px 20px;
        background-color: #FFF;
        border-radius: 12px;
        box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);
        transition: all 0.4s;
        &.open {         
          visibility: visible;
          transform: translateY(0);
        }
      }
      .ui-field {
        width: max-content;
      }
    }
  }
}

.main {
