.reviews {
  padding-top: 80px;
  padding-bottom: 80px;

  &-title {
    text-align: center;
    margin-bottom: 60px;     
  }

  &-slide {
    background: #FFF;
    border-radius: 12px;
    padding: 40px;
    box-shadow: 0px 6px 30px 0px rgba(43, 47, 137, 0.15);      
  }

  &-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 2px solid $bgc;

    .h4 {
      color: $font;
      @include fz(24);       
      font-weight: 600;
      line-height: 133.333%;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      max-width: 62%;
    }

    &-rating {
      svg:not(:last-of-type) {
        margin-right: 3px;
      }
      &.five {
        svg {
          fill: $bgc_pink;
        }
      }
      &.four {
        svg {
          &:nth-child(-n+4) {
            fill: $bgc_pink;
          }
        }
      }
      &.three {
        svg {
          &:nth-child(-n+3) {
            fill: $bgc_pink;
          }
        }
      }
      &.two {
        svg {
          &:nth-child(-n+2) {
            fill: $bgc_pink;
          }
        }
      }
      &.one {
        svg {
          &:nth-child(1) {
            fill: $bgc_pink;
          }
        }
      }
    }
  }

  &-main {
    color: $gray_dark;
    @include fz(16);           
    font-weight: 400;
    line-height: 156.25%;
    letter-spacing: 0.16px;
    height: 250px;
    overflow: auto;
    p {
      margin: 0;
    }
  }
}

