.accordion {
  &-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 20px;
    cursor: pointer;
    border-bottom: 2px solid $primary;
    transition: all 0.4s;

    &:hover {
      background-color: $primary;
      border-radius: 12px;
      color: #fff;
      .icon {
        background: $bgc;
      }
      svg {
        path {
          transition: all 0.5s;
          stroke: $primary;         
        }
      }
    }


    .title {
      @include fz(24);    
      font-weight: 600;
      line-height: 133.333%;
    }

    .icon {
      position: relative;
      width: 40px;
      height: 40px;
      border-radius: 50%;     
      background: var(--gradient-gradient-green, linear-gradient(180deg, #319DAC 0%, #278FAF 100%));
      transition: all 0.5s;

      &.open {
        background: $bgc;
        svg {
          path {
            transition: all 0.5s;
            stroke: $primary;
            &:first-child {
              display: none;
            }
          }
        }
      }

      svg {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &-content {
    max-height: 0; 
    overflow: hidden;
    transition: all 0.5s;

    .price-row {
      padding-top: 16px;
      padding-bottom: 11px;
      &:last-child {
        border-bottom: 2px solid #319DAC;
      }
      .name {
        @include fz(18);       
        line-height: 155.556%;
        letter-spacing: 0.36px;
      }
    }


    &.open {
      max-height: max-content;
    }
  }
}

